@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.EcoSystem {
  @include flex();
  width: 100vw;
}

.EcoSystemLeft,
.EcoSystemRight {
  @include flex();

  flex-direction: column;
  width: pw(480);
  height: 100vh;
  background: #002d71;
  color: #fff;
  padding: pw(40);

  img {
    cursor: pointer;
  }
}

.EcoSystemCenter {
  @include flex();
  width: calc(100vw - #{pw(960)});
  height: 100vh;
  flex-direction: column;
  color: #fff;
  padding: 120px 20px 0;
}

.EcosystemTitle {
  font-size: pw(48);
  color: #ffffff;
}

.Item {
  padding: 30px 0;
  user-select: none;
}

.ItemTitle {
  font-size: 23px;

  color: #14e0c8;
}

.ItemSecTitle {
  font-weight: 800;
  font-size: pw(36);
  opacity: 0.2;
}

.ItemContent {
  font-weight: 400;
  font-size: 14px;
  color: #ffffff;
  opacity: 0.7;
}

.ItemCurrent {
  border-top: 1px solid rgba($color: #33b3f4, $alpha: 0.1);
  border-bottom: 1px solid rgba($color: #33b3f4, $alpha: 0.1);
}

.ItemCurrent .ItemSecTitle {
  color: #ffffff;
  opacity: 1;
}

.EcosystemSvgWarp {
  @include flex;
  width: 100%;
  height: pw(700);
  overflow: hidden;
}

.EcosystemSvgContainer {
  transform-origin: center center;
}

.EcoSystemScaleWarp{
  @include flex();
  flex-direction: column;
}

@media (max-width: 768px) {
  .EcoSystem {
    @include flex();
    width: 100vw;
    flex-direction: column;
  }

  .EcosystemTitle {
    font-size: 24px;
    width: 100%;
    text-align: left;
  }

  .EcosystemSvgWarp {
    @include flex;
    width: 100%;
    height: 400px;
    // margin-top: 120px;
  }

  .EcoSystemCenter {
    @include flex();
    width: 100%;
    flex-direction: column;
    color: #fff;
    padding: 0 20px 0;
  }

  .MobileInfoList {
    // margin-top: -120px;
    padding: 0 30px;
  }
}
