@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';

.Team {
  @include flex();
}

.TeamLeft {
  @include flex(flex-end, flex-end);
  width: 50vw;
  height: 100vh;
  background: #002d71;
  padding: 30px;
}

.TeamRight {
  @include flex(flex-start);
  width: 50vw;
  height: 100vh;
}

.TeamItem {
  @include flex(flex-start);

  position: relative;
}

.TeamItemHeadWarp {
  width: 180px;
  height: 180px;
  overflow: hidden;
  margin-left: 20px;
  cursor: pointer;
}

.TeamItemHeadPreview {
  transform: scale(0.5);
  transform-origin: left top;
}

.TeamItemContainer {
  margin-top: pw(100);
}

.TeamItemContainerStart {
  @include flex(flex-start);
  margin-left: pw(-140);
}

.TeamItemContainerEnd {
  @include flex(flex-end);
}

.TeamItemHead {
  @include flex;

  width: pw(280);
  height: pw(280);
  background-size: 100% 100%;
  margin-right: pw(40);
  position: relative;
}

