.TeamItemHeadImage {
  width: 60%;
  height: 60%;
  position: absolute;
  z-index: 9;
}

.TeamItemHeadImageBg {
  width: 100%;
  height: 100%;
  z-index: 3;
  left: 0;
  top: 0;
}

.TeamItemInfo {
  width: pw(413);
}

.Name {
  font-weight: 900;
  font-size: pw(24);
  text-transform: capitalize;
  color: #ffffff;
}

.Post {
  font-weight: 400;
  font-size: pw(18);
  text-transform: capitalize;
  color: #ffffff;
  margin: pw(20) 0;
}

.Desc {
  font-weight: 400;
  font-size: pw(16);
  text-align: justify;
  color: var.$color-text;
}

@media (max-width: 768px) {
  .Team {
    padding: pw(120) 0 pw(320);
  }

  .TeamItemHead {
    @include flex;

    width: 150px;
    height: 150px;
  }

  .TeamItemInfo {
    width: calc(100% - 180px);
  }

  // .TeamItemContainer {
  //   width: 350px;
  // }

  .TeamItemContainerStart {
    margin-left: 0px;
  }

  .Team {
    flex-direction: column-reverse;
    & * {
      font-size: 14px !important;
    }
  }

  .TeamLeft {
    @include flex(center, flex-end);
    width: 100vw;
    background: #002d71;
    height: 20vh !important;
  }

  .TeamRight {
    @include flex(flex-start);
    width: 100vw;
    height: 80vh;
  }

  .TeamItemHeadWarp {
    width: 90px;
    height: 90px;
  }
}
