@import '../../assets/styles/Utils.scss';

.Resources {
  @include flex();
  box-sizing: border-box;
}

.ResourcesLeft {
  width: 50vw;
  height: 100vh;
  padding: pw(83) pw(60);
}

.ResourcesRight {
  width: 50vw;
  height: 100vh;
  padding: pw(83) pw(60);
  background: #002d71;
}

.SwiperItem {
  width: 100%;
  transform-origin: center;
  user-select: none;
}

.SwiperItemWarp {
  @include flex(flex-start);
  color: #fff;
  font-size: 18px;
}

.SwiperItemActive {
  color: #369feb;
}

.ResourcesTitle {
  font-weight: 900;
  font-size: pw(48);
  color: #ffffff;
}

.ResourcesContent {
  font-weight: 400;
  font-size: pw(18);
  text-align: left;
  opacity: 0.7;
  margin-bottom: pw(40);
  color: #ffffff;
  opacity: 0.7;
}

.Card {
  height: 800px;
  overflow: hidden;
}

.CardItemLogo {
  width: 100%;
  height: auto;
}

.SwiperItemActive .CardItem {
  background: radial-gradient(
    139.76% 139.76% at 50% -39.76%,
    #020f1c 0%,
    rgba(54, 159, 235, 0.46) 100%
  );
}

.CardItem {
  width: 360px;
  height: auto;
  border-radius: 20px;
  margin-right: 30px;
  background: #041f33;
  cursor: pointer;

  &:hover {
    background: radial-gradient(
      139.76% 139.76% at 50% -39.76%,
      #020f1c 0%,
      rgba(54, 159, 235, 0.46) 100%
    );
  }
}

.CardItemContentContainer {
  width: 100%;
  padding: pw(20) pw(30);
  min-height: 130px;
}

.CardItemTitle {
  font-weight: 900;
  font-size: 16px;
  text-transform: capitalize;
  color: #ffffff;
  word-break: break-all;
  margin-bottom: pw(10);
}

.CardItemContent {
  font-weight: 400;
  font-size: 14px;
  color: #ffffff;
  opacity: 0.7;
}

.ResourcesSecTitle {
  font-style: normal;
  font-weight: 900;
  font-size: pw(24);

  color: #ffffff;
  // margin-top: pw(100);
}

.ResourcesIcon {
  @include flex(flex-start);
  flex-wrap: wrap;
}

.ResourcesIconItem {
  width: pw(46);
  height: pw(46);
  margin-right: pw(8);
  position: relative;
}

.ResourcesIconImage {
  width: 100%;
  height: 100%;
}

.ResourcesIconItemContainer {
  transition: all 0.5s ease;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid transparent;

  &:hover {
    transform: scale(1.3) rotate(30deg);
    border: 1px solid #3f9a55;
  }
}

@media (max-width: 479px) {
  .CardItem {
    width: 100%;
    padding: pw(60);
    margin-bottom: pw(40);
  }

  .ResourcesIconItem {
    width: pw(146);
    height: pw(146);
    margin-right: pw(8);
    position: relative;
  }

  .Resources {
    flex-direction: column;
  }

  .ResourcesLeft {
    width: 100vw;
    height: 65vh;
    padding: pw(83) pw(60);
    padding-top: 15vw;
    box-sizing: border-box;
    overflow: hidden;
  }

  .ResourcesRight {
    width: 100vw;
    min-height: 50vh;
    padding: pw(83) pw(60);
    background: #002d71;
  }

  .ResourcesTitle {
    font-size: 18px;
  }

  .ResourcesSecTitle {
    font-size: 16px;
  }

  .ResourcesContent {
    font-size: 14px;
  }
}
